code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* GLOBAL STYLES
-------------------------------------------------- */
/* Padding below the footer and lighter body text */

body {
  /* padding-top: 3rem; */
  padding-bottom: 3rem;
  color: #5a5a5a;
}

a {
  text-decoration: none !important;
}

a.name-link:hover {
  text-decoration: none !important;
  color: #008080 !important;
}

a.name-link {
  text-decoration: none !important;
  color: #505050 !important;
}

/* CUSTOMIZE THE CAROUSEL
-------------------------------------------------- */

/* Carousel base class */
.carousel {
  margin-bottom: 4rem;
}
/* Since positioning the image, we need to help out the caption */
.carousel-caption {
  bottom: 3rem;
  z-index: 10;
}

/* Declare heights because of positioning of img element */
.carousel-item {
  height: 32rem;
}
.carousel-item > img {
  position: absolute;
  top: 0;
  left: 0;
  min-width: 100%;
  height: 32rem;
}

/* CUSTOMIZE THE DROPDOWN MENU FOR TRACKS
-------------------------------------------------- */

.tracks-container {
  float: left;
  display: none;
  position: absolute;
  background-color: #212529!important;
  list-style-type: none;
}

.tracks-container li{
  /* display: inline-block; */
  text-align: left;
}

.tracks-drop:hover .tracks-container{
  display: inline-block;
}

/* CUSTOMIZE THE PAGES FOR UAV-HUMAN TRACKS
-------------------------------------------------- */

.uav-human-track-header {
  /* display: inline-block; */
  /* margin: auto; */
  text-align: center;
}

.uav-human-container {
  min-height: 500px;
  margin: auto;
  width: 70%;
}

.uav-human-result-tables {
  margin: auto;
}

.uav-human-result-tables td{
  border: 1px solid;
  padding: 10px;
  border-spacing: 10px;
}

.uav-human-result-tables th{
  border: 1px solid;
  padding: 10px;
  border-spacing: 10px;
}

.uav-human-part-container {
  padding-top: 20px;
  padding-bottom: 20px;
  margin: auto;
  text-align: justify;
}


/* MARKETING CONTENT
-------------------------------------------------- */

/* Center align the text within the three columns below the carousel */
.marketing .col-lg-4 {
  margin-bottom: 1.5rem;
  text-align: center;
}
.marketing h2 {
  font-weight: 400;
}
/* rtl:begin:ignore */
.marketing .col-lg-4 p {
  margin-right: .75rem;
  margin-left: .75rem;
}
/* rtl:end:ignore */


/* Featurettes
------------------------- */

.featurette-divider {
  margin: 4rem 0; /* Space out the Bootstrap <hr> more */
}

/* Thin out the marketing headings */
.featurette-heading {
  font-weight: 300;
  line-height: 1;
  /* rtl:remove */
  letter-spacing: -.05rem;
}


/* RESPONSIVE CSS
-------------------------------------------------- */

@media (min-width: 40em) {
  /* Bump up size of carousel content */
  .carousel-caption p {
    margin-bottom: 1.25rem;
    font-size: 1.25rem;
    line-height: 1.4;
  }

  .featurette-heading {
    font-size: 50px;
  }
}

@media (min-width: 62em) {
  .featurette-heading {
    margin-top: 7rem;
  }
}
